
















































import { Vue, Component } from 'vue-property-decorator'
import Types from '../../../types/index'

@Component
export default class LoginNav extends Vue {
  protected userForm: Types.UserItem = {
    email: '',
    password: '',
  }
  protected form: any = {
    email: '',
    name: '',
    password: '',
  }
  protected loginShow: boolean = false
  protected timer: any = null

  protected goLogin(): void {
    this.$api
      .login(this.userForm)
      .then((req: Types.InterfaceData) => {
        const { code, data } = req
        if (code === 0) {
          this.$router.push({ path: '/' })
        } else {
          // this.$message.error(data)
          this.$notify({
            title: '提示',
            message: data,
            duration: 3000,
          })
        }
      })
      .catch((err: any) => {
        console.log(err)
      })
  }
  register() {
    this.$api
      .register(this.form)
      .then((req: Types.InterfaceData) => {
        const { code, data } = req
        console.log(data)
      })
      .catch((err: any) => {
        console.log(err)
      })
  }
  initbackground() {
    this.$nextTick(() => {
      //文字
      var txts: any = '0123456789!@#$%^&*()~_+℃☆○※'
      //转为数组
      txts = txts.split('')
      var matrix: any = document.getElementById('matrix')
      var context = matrix.getContext('2d')
      matrix.height = window.innerHeight
      matrix.width = window.innerWidth
      var drop: any = []
      var font_size = 16
      var columns = matrix.width / font_size
      for (var i = 0; i < columns; i++) drop[i] = 1

      function drawMatrix() {
        context.fillStyle = 'rgba(0, 0, 0, 0.09)'
        context.fillRect(0, 0, matrix.width, matrix.height)

        context.fillStyle = 'green'
        context.font = font_size + 'px'
        for (var i = 0; i < columns; i++) {
          //随机取要输出的文字
          var text = txts[Math.floor(Math.random() * txts.length)]
          //输出文字，注意坐标的计算
          context.fillText(
            text,
            i * font_size,
            drop[i] * font_size
          ) /*get 0 and 1*/

          if (
            drop[i] * font_size > (matrix.height * 2) / 3 &&
            Math.random() > 0.95
          )
            /*reset*/
            drop[i] = 0
          drop[i]++
        }
      }
      setTimeout(() => {
        this.loginShow = true
      }, 800)

      const timer = setInterval(drawMatrix, 33)
      this.$once('hook:beforeDestroy', () => {
        clearInterval(timer)
      })
    })
  }
  mounted() {
    this.initbackground()
    window.addEventListener('resize', this.initbackground)
  }
  destroyed() {
    window.removeEventListener('resize', this.initbackground, true)
  }
}
